<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-12 card">
          <card class="card-plain">
            <div class="row">
              <div class="col-6"><h4 class="card-title">Add New Maintenance</h4></div>
            </div>
            <form>
              <div class="row">
              <div class="col-md-6">
                <label>Select Lable</label>
                <select class="form-control" v-model="form.type">
                  <option>Select Label</option>
                  <option v-for="(index, i) in categories" :key="i" :value="i">{{ index }}</option>
                </select>
                <div v-if="isSubmitted && $v.form.type.$error" class="invalid-feedback">
                  <span v-if="!$v.form.type.required">Type field is required.</span>
                </div>
              </div>
              <div class="col-md-12">
                <label>Short Descriptions</label>
                <textarea class="form-control" v-model="form.description"></textarea>
                <div v-if="isSubmitted && $v.form.description.$error" class="invalid-feedback">
                  <span v-if="!$v.form.description.required">Description field is required.</span>
                </div>
              </div>
              <div class="col-md-6">
                <label>Maintenance Video</label><br>
                  <div class="dropbox">
                    <input type="file" accept="video/mp4,video/x-m4v,video/*" multiple  @change="onFileChange" class="input-file">
                      <p>
                        Drag your file(s) here to begin<br> or click to browse
                      </p>
                      <div class="row">
                        
                      </div> 
                  </div>
              </div>
              <div class="col-md-6" v-if="form.name">
                        <video
                        :key="video"
                        width="450"
                        controls
                      >
                        <source
                          :src="imagePath+form.name"
                          type="video/mp4"
                        >
                      </video>
                      </div> 
            </div>
            <ajax-loader v-if="loading"></ajax-loader>

            <div class="row mt-4">
                  <div class="col-md-12">
                    <router-link :to="{ path: '/admin/maintenance/'+ form.property_id}">Back</router-link>
                    <button type="submit" class="btn btn-info btn-fill float-right" @click.prevent="saveProperty">
                      Save
                    </button>
                  </div>
                </div>
            </form>
          </card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from "axios";
  import LTable from 'src/components/Table.vue'
  import Card from 'src/components/Cards/Card.vue'
import BaseInput from '../../components/Inputs/BaseInput.vue';
import AjaxLoader from '../../AjaxLoader.vue';

import {
      required
  } from "vuelidate/lib/validators";
  export default {
    components: {
      LTable,
      Card,
        BaseInput,
        AjaxLoader
    },
    data () {
      return {
        isSubmitted:false,
        categories: '',
        imagePath:'',
        tableData : [],
        loading: false,
        form:{
          property_id:'',
          type:'',
          name:'',
          description:'',
          video:''
        }
      }
    },
    validations: {
      form: {
        property_id:{required},
        type: {required},
        name:{required},
        description:{required},
      }
    },
    mounted (){
      this.imagePath = process.env.VUE_APP_IMAGE
      this.form.property_id = this.$route.params.id
      this._getCategory();
    },
    methods: {
      _getCategory(){
        let currentObj = this;
        axios.get(process.env.VUE_APP_API_URL+'maintaince-categories', {
            headers: {
                'Authorization' : 'Bearer '+ localStorage.getItem('token')
            }, 
          }).then(function(response){
            currentObj.categories = response.data.categories
        }).catch(error => {
          console.log(error)
        }).finally( () => {

        })
      },
      onFileChange(event){
        event.preventDefault();
          let currentObj = this;
          this.loading = true
          const config = {
              headers: { 'content-type': 'multipart/form-data' }
          }
          let formData = new FormData();
          formData.append('video', event.target.files[0]);
          formData.append('path', 'maintenance');
          formData.append('key', 'id_proof');
          this.loadingRear = true
          axios.post(process.env.VUE_APP_API_URL+'upload_video', formData, config)
          .then(function (response) {
              if(response.data.status){
                currentObj.form.name = response.data.url
              }
              currentObj.loading = false
          })
        .catch(error => {
          this.$notifications.notify(
                {
                  message: `<span>`+error+`</span>`,
                  icon: 'nc-icon nc-bell-55',
                  horizontalAlign: 'right',
                  verticalAlign: 'top',
                  type: 'danger'
                })
        }).finally( () => {
          currentObj.loading=false;
        })
      },
      saveProperty () {
        this.isSubmitted = true;
        this.$v.$touch();
        if (this.$v.$invalid) {
            return;
        }
        this.loading=true;
        const url = process.env.VUE_APP_API_URL+'add-maintaince-video'
        axios.post(url, this.form, {
            headers: {
                'Authorization' : 'Bearer '+ localStorage.getItem('token')
            },
            })
            .then(response => {
              this.loading=false;
              this.isSubmitted=false;
              console.log(response.status)
              if(response.status == 200 && response.data){
                this.$notifications.notify(
                {
                  message: '<span>'+response.data.message+'</span>',
                  icon: 'nc-icon nc-bell-55',
                  horizontalAlign: 'right',
                  verticalAlign: 'top',
                  type: 'success'
                })
                this.$router.push({name: 'AddMaintenance', 'params': {'id': this.form.property_id}});
              }else{
                this.$notifications.notify(
                {
                  message: '<span>'+response.data.error_message+'</span>',
                  icon: 'nc-icon nc-bell-55',
                  horizontalAlign: 'right',
                  verticalAlign: 'top',
                  type: 'danger'
                })
              }
           
            })
            .catch(error => {
              this.$notifications.notify(
              {
                message: `<span>Something went wrong.</span>`,
                icon: 'nc-icon nc-bell-55',
                horizontalAlign: 'right',
                verticalAlign: 'top',
                type: 'danger'
              })
                this.isSubmitted=false;
                this.loading=false;
            }).finally( () => {
                this.isSubmitted = false
                this.loading=false;
            })
      }
    }
  }
</script>
<style>
span.delete {
    cursor: pointer;
}
</style>
