<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-12">
          <card class="card-plain">
            <div class="row">
              <div class="col-6"><h4 class="card-title">Maintenance Videos</h4></div>
              <div class="col-6">
                <router-link class="d-flex justify-content-end" :to="{ path: '/admin/add-maintenance/'+ id}">Add Maintenance</router-link>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <card v-for="(index, i) in tableData" :key="i">
                  <ul  class="list-group" :key="i">
                    <slot v-for="(cat, c) in categories"><p :key="c" v-if="c==i">{{ cat }}</p></slot>
                  <slot v-for="(item, k) in index">
                    <div :key="k+i">
                      <div class="d-flex justify-content-end mb-2 mt-2">
                        <router-link class="btn btn-info" :to="{ path: '/admin/edit-maintenance/'+ id + '/'+item.id }">Edit</router-link>
                        <button type="button"  class="btn btn-danger ml-2" @click.prevent="deleteMaintenance(item.id)">Delete</button>
                      </div>
                    
                    <li class="list-group-item" v-if="item.name">
                      <video
                        :key="video"
                        width="450"
                        controls
                      >
                        <source
                          :src="imagePath+item.name"
                          type="video/mp4"
                        >
                      </video>
                    </li>
                    <li class="list-group-item">Description: {{ item.description }}</li>
                    </div>
                 
                  </slot>
                  </ul>
                </card>
                </div>
            </div>
          </card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from "axios";
  import LTable from 'src/components/Table.vue'
  import Card from 'src/components/Cards/Card.vue'
  export default {
    components: {
      LTable,
      Card
    },
    data () {
      return {
        categories:'',
        video:'',
        id:'',
        imagePath:'',
        tableData : [],
        loading: false
      }
    },
    mounted (){
      this.id=this.$route.params.id
      this.imagePath = process.env.VUE_APP_IMAGE
      this._getMaintenance(this.$route.params.id);
      this._getCategory();

    },
    methods: {
      _getCategory(){
        let currentObj = this;
        axios.get(process.env.VUE_APP_API_URL+'maintaince-categories', {
            headers: {
                'Authorization' : 'Bearer '+ localStorage.getItem('token')
            }, 
          }).then(function(response){
            currentObj.categories = response.data.categories
        }).catch(error => {
          console.log(error)
        }).finally( () => {

        })
      },
      _getMaintenance(id){
        let currentObj = this;
        axios.get(process.env.VUE_APP_API_URL+'maintaince-videos/'+id, {
            headers: {
                'Authorization' : 'Bearer '+ localStorage.getItem('token')
            }, 
          }).then(function(response){
            currentObj.tableData = response.data.videos
        }).catch(error => {
          console.log(error)
        }).finally( () => {

        })
      },
      deleteMaintenance(id){
        if(confirm("Do you really want to delete?")){
          axios.get(process.env.VUE_APP_API_URL+'delete-maintaince-video/'+id, {
              headers: {
                  'Authorization' : 'Bearer '+ localStorage.getItem('token')
              }, 
            }).then(function(response){
              location.reload()
          }).catch(error => {
            console.log(error)
          }).finally( () => {

          })
        }
      }
    }
  }
</script>
<style>
span.delete {
    cursor: pointer;
}
</style>
